@charset "UTF-8";
/*------------------------------------*\
    Variables
\*------------------------------------*/
.accordion-toggle {
  width: 100%;
  text-align: left;
}

.faq_content_box {
  max-width: 1200px;
  margin: 0 auto;
  margin-bottom: 6rem !important;
  padding: 0 2rem;
}
.faq_content_box h2 {
  font-size: 4rem !important;
  text-align: center;
  margin-bottom: 1rem;
}
.faq_content_box {
  margin-top: 2rem !important;
}
.faq_content_box article {
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  background: #fff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.06);
  overflow: hidden;
  margin: 12px 0;
}
.faq_content_box .accordion-toggle {
  display: block;
  font-size: 20px !important;
  background-color: transparent !important;
  width: 100%;
  border: none;
  cursor: pointer;
  padding: 16px 18px;
  color: #10a0a2 !important;
  line-height: 1.35;
  position: relative;
}
.faq_content_box .accordion-toggle:focus-visible {
  outline: 2px solid #2563eb;
  outline-offset: 2px;
  border-radius: 6px;
}
.faq_content_box .accordion-toggle .accordion-title {
  display: block;
  font-weight: 600;
  color: #58a6a8 !important;
  margin-right: 12px;
}
.faq_content_box .accordion-toggle .icon {
  width: 20px;
  height: 20px;
  fill: currentColor;
  color: #6b7280;
  transition: transform 0.25s ease, color 0.2s ease;
  flex: 0 0 auto;
  position: absolute;
  right: 10px;
  top: 20px;
}
.faq_content_box .accordion-toggle[aria-expanded=true] .icon {
  transform: rotate(180deg);
  color: #111827;
}
.faq_content_box .accordion-content {
  padding: 20px 18px 16px;
  color: #374151;
}
.faq_content_box .accordion-content[hidden] {
  display: none !important;
}
.faq_content_box .accordion-content > :first-child {
  margin-top: 12px;
}

.legacy-callout {
  background: linear-gradient(var(--grad-direction, to right), var(--grad-start, #1c5151), var(--grad-end, #0f8a8b));
  color: #fff;
  text-align: center;
  padding: 30px !important;
  position: relative;
  overflow: hidden;
}
.legacy-callout.is-full {
  width: 100%;
  margin-inline: auto;
}
.legacy-callout.is-half-md {
  width: 100%;
  margin-inline: auto;
}
@media (min-width: 768px) {
  .legacy-callout.is-half-md {
    width: 50%;
  }
}
.legacy-callout__inner {
  max-width: 920px;
  padding: inherit !important;
  margin: 0 auto !important;
  padding-inline: 16px;
}
.legacy-callout__content h1,
.legacy-callout__content h2,
.legacy-callout__content h3,
.legacy-callout__content h4,
.legacy-callout__content p {
  color: #fff !important;
  margin: 0 0 0.8em;
}
.legacy-callout__content p {
  font-size: clamp(18px, 2.2vw, 22px);
  line-height: 1.6;
  font-weight: 400;
  color: #fff !important;
}
.legacy-callout__content p strong,
.legacy-callout__content p b {
  font-weight: 700;
}
.legacy-callout__content h2,
.legacy-callout__content h3 {
  line-height: 1.35;
  color: #fff !important;
  font-size: 32px !important;
  padding: 0 !important;
}
.legacy-callout__content h2 strong,
.legacy-callout__content h2 b,
.legacy-callout__content h3 strong,
.legacy-callout__content h3 b {
  font-weight: 900 !important;
}
.legacy-callout__cta {
  margin-top: 18px;
}
.legacy-callout .btn-cta {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  padding: 18px 24px;
  border-radius: 8px;
  background: #c13b5a;
  color: #fff;
  text-decoration: none;
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.15), 0 8px 18px rgba(0, 0, 0, 0.15);
  transition: transform 0.1s ease, box-shadow 0.2s ease, filter 0.2s ease;
}
.legacy-callout .btn-cta:hover {
  filter: brightness(1.05);
}
.legacy-callout .btn-cta:active {
  transform: translateY(1px);
}
.legacy-callout .btn-cta__icon {
  font-size: 30px;
  line-height: 1;
  display: inline-block;
  transform: translateY(-1px);
}
.legacy-callout .btn-cta__label {
  font-weight: 700;
  font-size: 24px;
  line-height: 1;
  white-space: nowrap;
}

/* --- Background-image mode (fallback stays your gradient) --- */
.legacy-callout {
  position: relative;
  overflow: hidden;
}
.legacy-callout.has-image {
  /* kill gradient when an image is present */
  background: none;
  /* bg layer */
}
.legacy-callout.has-image .legacy-callout__bg {
  position: absolute;
  inset: 0;
  background-image: var(--bg);
  background-size: cover;
  background-position: var(--bg-pos, center);
  background-repeat: no-repeat;
  transform: scale(1.01);
  z-index: 0;
}
.legacy-callout.has-image {
  /* optional darken overlay */
}
.legacy-callout.has-image .legacy-callout__overlay {
  position: absolute;
  inset: 0;
  background: var(--bg-overlay, rgba(0, 0, 0, 0.35));
  z-index: 1;
}
.legacy-callout.has-image {
  /* content above bg/overlay */
}
.legacy-callout.has-image .legacy-callout__inner {
  position: relative;
  z-index: 2;
}
.legacy-callout {
  /* Use mobile image when provided */
}
@media (max-width: 767.98px) {
  .legacy-callout.has-image .legacy-callout__bg {
    background-image: var(--bg-mobile, var(--bg));
  }
}

.project-pills {
  --pill-text: #10979a;
  --pill-bg: #fff;
  --pill-border: #ececec;
  --pill-hover-bg: #10979a;
  --pill-hover-text: #fff;
  background: #f5f6f4;
  padding: 22px 18px;
}
.project-pills__inner {
  display: grid;
  grid-template-columns: 1fr;
  gap: 14px 24px;
  max-width: 1200px;
  margin: 0 auto !important;
  padding: 15px !important;
}
.project-pills__title h3 {
  margin: 0;
  font-weight: 700;
  line-height: 1.25;
  padding: 0;
}
.project-pills__list {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  margin: 0;
  padding: 0;
  list-style: none;
}
.project-pills .project-pill {
  display: inline-block;
  background: var(--pill-bg);
  color: var(--pill-text);
  border: 1px solid var(--pill-border);
  padding: 8px 12px;
  font-weight: 700;
  text-decoration: none;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.05);
  transition: background 0.2s ease, color 0.2s ease, box-shadow 0.2s ease;
}
.project-pills .project-pill:hover {
  background: var(--pill-hover-bg);
  color: var(--pill-hover-text);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
}
@media (min-width: 900px) {
  .project-pills__inner {
    grid-template-columns: 280px 1fr;
    align-items: start;
  }
}

.project-cards-grid {
  --card-radius: 12px;
  --card-shadow: 0 6px 16px rgba(0, 0, 0, .08);
  --card-border: #10a0a2;
  --cta-bg: #10a0a2;
  --cta-text: #fff;
  border-radius: var(--card-radius);
  overflow: hidden !important;
}
.project-cards-grid__header {
  max-width: 1200px !important;
  margin: 0 auto 16px !important;
  padding: 0 8px !important;
}
.project-cards-grid__header h2 {
  margin: 0;
  font-weight: 700;
}
.project-cards-grid__list {
  max-width: 1200px !important;
  margin: 0 auto !important;
  padding: 60px 20px !important;
  list-style: none !important;
  display: grid !important;
  gap: 18px !important;
  grid-template-columns: repeat(1, minmax(0, 1fr));
}
@media (min-width: 700px) {
  .project-cards-grid__list {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}
@media (min-width: 1024px) {
  .project-cards-grid__list {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}
.project-cards-grid .project-card {
  background: #fff;
  border: 1px solid var(--card-border);
  border-radius: var(--card-radius);
  box-shadow: var(--card-shadow);
  overflow: hidden;
}
.project-cards-grid .project-card__link {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  height: 100%;
  color: inherit;
  text-align: center;
}
.project-cards-grid .project-card__media {
  position: relative;
  aspect-ratio: 16/12;
  background: #f3f4f6;
}
.project-cards-grid .project-card__media img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.project-cards-grid .project-card__media .project-card__icon {
  position: absolute;
  left: 50%;
  bottom: -18px;
  transform: translateX(-50%);
  width: 62px;
  height: 62px;
  border-radius: 999px;
  background: #1aa8a5;
  display: grid;
  place-items: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  border: 3px solid #fff;
}
.project-cards-grid .project-card__media .project-card__icon img {
  width: 56px;
  height: 56px;
  height: 56px;
}
.project-cards-grid .project-card__body {
  padding: 32px 16px 16px;
  display: grid;
  gap: 10px;
  align-content: start;
}
.project-cards-grid .project-card__body .project-card__title {
  font-weight: 700;
  margin: 0;
  padding: 0 !important;
  font-size: 22px;
}
.project-cards-grid .project-card__body .project-card__cta {
  display: inline-block;
  align-self: center;
  padding: 10px 14px;
  border-radius: 8px;
  background: var(--cta-bg);
  color: var(--cta-text);
  font-weight: 700;
}

/* --- Mobile carousel polish (Slick) --- */
.project-cards-grid .project-cards-grid__list.slick-initialized {
  display: block;
  padding: 0rem 36px 4rem 36px;
  max-width: 720px;
  margin: 0 auto;
}
.project-cards-grid .slick-dots {
  display: none !important;
  bottom: -10px;
}
.project-cards-grid .slick-dots li {
  margin: 0 3px;
}
.project-cards-grid .slick-dots li button:before {
  font-size: 8px;
  opacity: 0.35;
  color: #0ea5a4;
}
.project-cards-grid .slick-dots li.slick-active button:before {
  opacity: 0.9;
}
.project-cards-grid .slick-arrow {
  position: absolute;
  top: 42%;
  transform: translateY(-50%);
  width: 44px;
  height: 44px;
  border-radius: 50%;
  background: #10a0a2 !important;
  border: 2px solid rgba(0, 0, 0, 0.08);
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.12);
  z-index: 3;
}
.project-cards-grid .slick-arrow:before {
  color: #fff;
  font-size: 22px;
  line-height: 44px;
  opacity: 1;
}
.project-cards-grid .slick-prev {
  left: 5px;
}
.project-cards-grid .slick-next {
  right: 5px;
}
.project-cards-grid .project-card {
  border-radius: var(--card-radius);
}
.project-cards-grid .project-card__media {
  border-top-left-radius: var(--card-radius);
  border-top-right-radius: var(--card-radius);
}
.project-cards-grid .project-card__body {
  padding-top: 36px;
}
.project-cards-grid .project-card .project-card__icon {
  bottom: -22px;
  width: 68px;
  height: 68px;
}
.project-cards-grid .project-card .project-card__icon img {
  width: 60px;
  height: 60px;
}
@media (min-width: 768px) {
  .project-cards-grid .project-cards-grid__list.slick-initialized {
    padding: 20px;
  }
  .project-cards-grid .project-cards-grid__list.slick-initialized .slick-list {
    overflow: hidden;
  }
  .project-cards-grid .slick-arrow {
    display: none !important;
  }
  .project-cards-grid .slick-dots {
    display: none !important;
  }
}

.two-up-hero {
  margin: 4rem 0;
  --accent: #18a6a5;
  --text: #111;
  color: var(--text);
}
.two-up-hero__grid {
  display: grid;
  gap: 24px;
  align-items: center;
  grid-template-columns: 1fr;
  max-width: 1200px;
  margin: 0 auto !important;
  padding: 60px 30px !important;
}
@media (min-width: 900px) {
  .two-up-hero__grid {
    grid-template-columns: 1fr 1fr;
    gap: 36px;
  }
}
.two-up-hero {
  /* Single image (circle/rounded/square) */
}
.two-up-hero__photo {
  aspect-ratio: 1/1;
  overflow: hidden;
}
.two-up-hero__photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.two-up-hero__photo.is-circle {
  border-radius: 9999px;
}
.two-up-hero__photo.is-rounded {
  border-radius: 18px;
}
.two-up-hero__photo.is-square {
  border-radius: 0;
}
.two-up-hero {
  /* Collage (4 tiles) */
}
.two-up-hero__collage {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
}
.two-up-hero__collage .two-up-hero__tile {
  border-radius: 16px;
  overflow: hidden;
  background: #f3f4f6;
}
.two-up-hero__collage .two-up-hero__tile img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.two-up-hero__collage .two-up-hero__tile--2, .two-up-hero__collage .two-up-hero__tile--3 {
  aspect-ratio: 16/11;
}
.two-up-hero__collage .two-up-hero__tile--1, .two-up-hero__collage .two-up-hero__tile--4 {
  aspect-ratio: 16/12;
}
@media (min-width: 900px) {
  .two-up-hero__collage {
    gap: 14px;
  }
}
.two-up-hero__heading {
  margin: 0 0 1rem !important;
  font-weight: 800 !important;
  font-size: 36px;
  line-height: 1;
}
@media (max-width: 768px) {
  .two-up-hero__heading {
    text-align: center;
  }
}
.two-up-hero__heading .h-top {
  display: block;
  font-size: clamp(22px, 3vw, 30px);
}
.two-up-hero__heading .h-main {
  font-size: clamp(28px, 4vw, 42px);
}
.two-up-hero__heading .h-accent {
  font-size: clamp(28px, 4vw, 42px);
  color: var(--accent);
}
@media (max-width: 768px) {
  .two-up-hero p {
    text-align: center;
  }
}
.two-up-hero__body p {
  margin: 0 0 0.75em;
  font-size: clamp(16px, 2vw, 18px);
  line-height: 1.1;
  padding: 0;
}
.two-up-hero__body h2 {
  font-weight: normal !important;
  margin-bottom: 0;
  font-size: 18px;
  line-height: 1.4;
  padding-top: 0 !important;
}
@media (max-width: 768px) {
  .two-up-hero__body h2 {
    text-align: center;
  }
}
.two-up-hero__body h2 b,
.two-up-hero__body h2 strong {
  font-weight: 900;
}
.two-up-hero .btn-cta {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  padding: 18px 24px;
  border-radius: 8px;
  background: #c13b5a;
  color: #fff;
  text-decoration: none;
  font-weight: 700;
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.12), 0 8px 18px rgba(0, 0, 0, 0.12);
  transition: transform 0.1s ease, filter 0.2s ease;
}
.two-up-hero .btn-cta:hover {
  filter: brightness(1.05);
}
.two-up-hero .btn-cta:active {
  transform: translateY(1px);
}
.two-up-hero .btn-cta__icon {
  font-size: 30px;
  line-height: 1;
  display: inline-block;
  transform: translateY(-1px);
}
.two-up-hero .btn-cta__label {
  font-weight: 700;
  font-size: 24px;
  line-height: 1;
  white-space: nowrap;
}
.two-up-hero__grid {
  display: grid;
  grid-template-columns: 1fr;
}
.two-up-hero__media {
  order: 1;
}
.two-up-hero__content {
  order: 0;
}
.two-up-hero__media {
  margin-bottom: 16px;
}
.two-up-hero__content {
  margin-top: 0;
}
@media (min-width: 900px) {
  .two-up-hero__grid {
    grid-template-columns: 1fr 1fr;
  }
  .two-up-hero__media, .two-up-hero__content {
    order: initial;
  }
  .two-up-hero__media {
    margin-bottom: 0;
  }
}

.impact-stats {
  /* same gradient system as your other blocks */
  background: linear-gradient(var(--grad-angle, 90deg), var(--grad-start, #0f8a8b), var(--grad-end, #1fb0a5));
  color: #fff;
  text-align: center;
  padding: 4rem 2rem !important;
}
.impact-stats__inner {
  max-width: 1200px;
  margin: 0 auto !important;
}
.impact-stats__headline {
  margin: 0 0 0.4em;
  font-weight: 800;
  font-size: clamp(28px, 5vw, 48px);
  line-height: 1.15;
  color: #fff;
}
.impact-stats__lede {
  margin: 0 auto;
  max-width: 960px;
  font-size: 36px !important;
  line-height: 1.65;
}
.impact-stats__lede--top {
  opacity: 0.95;
}
.impact-stats__lede--bottom {
  font-weight: 600 !important;
  margin-bottom: 2rem !important;
}
.impact-stats__grid {
  margin-top: clamp(20px, 4vw, 40px);
  display: grid;
  gap: clamp(16px, 3vw, 28px);
  grid-template-columns: repeat(1, minmax(0, 1fr));
}
@media (min-width: 900px) {
  .impact-stats__grid.impact-stats__grid--cols-2 {
    grid-template-columns: repeat(2, 1fr);
  }
  .impact-stats__grid.impact-stats__grid--cols-3 {
    grid-template-columns: repeat(3, 1fr);
  }
  .impact-stats__grid.impact-stats__grid--cols-4 {
    grid-template-columns: repeat(4, 1fr);
  }
}
.impact-stats__item {
  list-style: none;
  position: relative;
  padding: clamp(10px, 2vw, 14px) clamp(8px, 2vw, 14px);
  /* vertical dividers on desktop */
}
@media (min-width: 900px) {
  .impact-stats__item:not(:nth-child(3n+1)) {
    border-left: 1px solid rgba(255, 255, 255, 0.35);
  }
}
.impact-stats p {
  font-size: 18px !important;
  color: #fff !important;
  padding: 0 !important;
  margin-bottom: 1rem !important;
}
.impact-stats__value {
  font-weight: 800;
  font-size: 50px !important;
  letter-spacing: 0.5px;
  color: #fff !important;
}
@media (max-width: 768px) {
  .impact-stats__label {
    padding-bottom: 1rem !important;
    border-bottom: 1px solid #fff !important;
  }
}
.impact-stats__label .l1 {
  font-weight: 600;
  opacity: 0.95;
  color: #fff !important;
}
.impact-stats__label .l2 {
  margin-top: 0.6em;
  padding-top: 0.6em;
  border-top: 1px solid rgba(255, 255, 255, 0.35);
  opacity: 0.95;
}

.map-cta__inner {
  max-width: 1200px;
  margin: 0 auto;
}
.map-cta__grid {
  display: grid;
  gap: clamp(16px, 3vw, 32px);
  /* Mobile: content first, map last */
  grid-template-areas: "content" "map";
}
.map-cta__content {
  grid-area: content;
}
.map-cta__embed {
  grid-area: map;
}
.map-cta {
  /* Desktop/tablet up: two columns, map first */
}
@media (min-width: 900px) {
  .map-cta__grid {
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "map content";
    align-items: stretch;
  }
}

