.impact-stats.has-image {
  background: none; /* disable gradient when image provided */
}
.impact-stats.has-image .impact-stats__bg {
  position: absolute;
  inset: 0;
  background-image: var(--bg);
  background-size: cover;
  background-position: var(--bg-pos, center);
  background-repeat: no-repeat;
  transform: scale(1.01);
  z-index: 0;
}
.impact-stats.has-image .impact-stats__overlay {
  position: absolute;
  inset: 0;
  background: var(--bg-overlay, rgba(0, 0, 0, 0.35));
  z-index: 1;
}
.impact-stats.has-image .impact-stats__inner {
  position: relative;
  z-index: 2;
  color: #fff;
  margin-bottom: 4rem !important;
}
.impact-stats.has-image .impact-stats__inner .impact-stats__headline,
.impact-stats.has-image .impact-stats__inner .impact-stats__lede {
  color: #fff;
  font-weight: bold !important;
  display: inline-block !important;
}
.impact-stats {
  /* mobile: prefer mobile bg if provided */
}
@media (max-width: 767.98px) {
  .impact-stats.has-image .impact-stats__bg {
    background-image: var(--bg-mobile, var(--bg));
  }
}

.map-cta {
  margin: 4rem 0;
  --teal: #18a6a5;
  --rose: #c21f4f;
  --text: #111;
}
.map-cta .map-cta__inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}
.map-cta .map-cta__grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  align-items: start;
}
